<template>
  <div>
    <p class="page" ref="extract" :class="showContinue ? 'page--continue' : ''">{{extract}}</p>
  </div>
</template>

<script>

/* If required */
/* import otherComponent from './components/OtherComponent' */

export default {
  name: 'page',
  props: ['extract'],
  data() {
    return {
      showContinue: false
    }
  },
  mounted() {
    const scrollHeight = this.$refs.extract.scrollHeight
    const clientHeight = this.$refs.extract.clientHeight
    this.showContinue = scrollHeight > clientHeight
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped lang='scss'>
$lineHeight: 1.5em;
$height: 1.5em * 8;
.page {
  position: relative;
  margin: 15px 15px;
  text-align: justify;
  font-size: 16px;
  line-height: $lineHeight;
  /* *3 */
  height: $height;
  overflow: hidden;
}

.page--continue:after {
  content: "...";
  position: absolute;
  font-weight: bold;
  bottom: 3px;
  right: 0;
  padding: 0 9px;
  background-color: white;
}
</style>



